<template>
  <div>

      <div :class="{ topClass: isTob }" class="topClass1">
        <div
        @click="butTab(item.id)"
          v-for="(item, index) in tabList"
          :key="index"
          class="tabText"
          :class="{ selectTabText: tabId == item.id }"
        >
          {{ item.name }}
          <div v-if="tabId == item.id" class="bottom"></div>
        </div>
      </div>

  </div>
</template>

<script>
export default {
  props: {
    tabList: Array,
    tabId: String,
    isTob: Boolean,
  },
  data() {
    return {

    };
  },
  methods:{
    butTab(id){
      this.$emit('butTab',id)
    }
  }
};
</script>

<style lang="scss" scoped>
.tabText {
  color: #999999;
  font-size: 14px;
  line-height: 20px;
}
.bottom {
  width: 19px;
  height: 3px;
  border-radius: 22px;
  opacity: 1;
  margin: 0 auto;
  margin-top: 5px;
  position: relative;
  // bottom: 5rpx;
  background: #00599D;
  opacity: 1;
}
.topClass {
  position: fixed;
  top: 0px;
  left: 0px;
  z-index: 9;
  border-radius: 0 !important;
  width: 100%;
}
.topClass1 {
  display: flex;
  justify-content: space-around;
  background-color: #fff;
  margin-bottom: 10px;
  padding: 10px 0;
  border-radius: 10px;
}
.selectTabText {
  color: #00599D;
}
</style>



