<template>
  <div class="svgIcon">
    <van-image
      :width="width"
      :height="height"
      class="mr-5"
      :src="src"
    ></van-image>
   <slot name="content"></slot>
  </div>
</template>

<script>
export default {
  props:{
    src:String,
    width:{
      type:String,
      default:'20'
    },
    height:{
      type:String,
      default:'24'
    }
  }
};
</script>

<style less="scss" scoped>
.svgIcon{
  display: flex;
  align-items: center;
}
.mr-5{
  margin-right: 5px;
}
</style>



