<template>
  <div class="p-all-12 bg-fff br-8 mb-12 six-2-box">
    <div v-if="info.type == 1">
      <div class="ta-c">
        <van-image
          class="mb-12"
          width="153"
          height="39"
          :src="require('@/assets/tempfile/aj/headerImage/yswl.png')"
        ></van-image>
