          v-for="(item, index) in info.planList"

        >
          <div style="display: flex; align-items: center; margin-bottom: 8px;color: #0E58A3;">
            <div>{{(index<10?'0':'')+ (index + 1) }}</div>
            {{ item.title }}
          </div>
          <div style="display: flex; justify-content: space-between">
            <div
              v-for="(item2, index2) in item.imageUrl"

              v-if="index2 <= 1"
            >
              <router-link
                :to="{
                  path: '/solutionInfo',
                  name: 'solutionInfo',
                  params: {
                    id: item2.id,
                  },
                }"
              >
                <div style="text-align: center">
                  <van-image
                    width="149"
                    height="93"
                    fit="fill"
                    :src="item2.imageUrl"
                  />
                  <!-- <div
                    style="
                      color: #fff;
                      background-color: rgba(14, 88, 163, 0.68);
                      padding: 5px;
                    "
                  >
                    <p class="ml-10 fs-14">{{ item2.name }}</p>
                  </div> -->
                </div>
              </router-link>

            </div>
          </div>
        </div>
        <router-link :to="{
                  path: '/secondIndex',
                  name: 'secondIndex',
                }">
             <div style="text-align: right;color:#000000;font-size: 12px;">更多<van-icon name="arrow"></van-icon></div>
              </router-link>
      </div>
    </div>
    <div v-if="info.type == 2">
      <div class="ta-c">
        <van-image
          class="mb-12"
          width="153"
          height="39"
          :src="require('@/assets/tempfile/aj/headerImage/jjfa.png')"
        ></van-image>
      </div>
      <div style="display: flex; flex-wrap: wrap;justify-content: space-between;">
        <router-link style="margin-bottom:10px"  v-for="(item, index) in info.planList" :key="index" :to="{path:'/solutionInfo',name:'solutionInfo',params:{
          id:item.id
        }}">
            <div style="display: flex;align-items: center; color:#333">
              <div class="shape"></div>
              {{ item.title }}
            </div>
          <van-image width="154" height="110" :src="item.imageUrl"></van-image>
      </router-link>
      </div>
      <router-link :to="{
                  path: '/fourthIndex',
                  name: 'fourthIndex',
                }">
             <div style="text-align: right;color:#000000;font-size: 12px;">更多<van-icon name="arrow"></van-icon></div>
              </router-link>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    info: Object,
  },
};
</script>

<style lang="scss" scoped>
.b-0 {
  bottom: 0;
}
.r-0 {
  right: 0;
}
.planBox1 {
  width: 100%;
  height: 160px;
  background-repeat: no-repeat;
  background-size: cover;
  margin-bottom: 9px;
  position: relative;
  overflow: hidden;
  border-radius: 4px;
  .mask {
    position: absolute;
    bottom: 0px;
    left: 0px;
    background: rgba(0, 0, 0, 0.4);
    height: 33px;
    padding: 8px;
    font-size: 12px;
    color: #fff;
    width: 100%;
  }
}
.planBox2 {
  width: 100%;
  height: 160px;
  background-repeat: no-repeat;
  background-size: cover;
  margin-bottom: 9px;
  position: relative;
  overflow: hidden;
  .mask {
    position: absolute;
    top: 0px;
    left: 0px;
    background: rgba(0, 0, 0, 0.4);
    height: 100%;
    padding: 8px;
    font-size: 12px;
    color: #fff;
    width: 100%;
    padding: 15px 12px;
    .borline {
      padding-bottom: 4px;
      border-bottom: 2px solid #af6c1a;
    }
    .content {
      font-weight: 400;
      color: rgba(255, 255, 255, 0.8);
    }
  }
}
.shape {
  width: 8px;
  height: 8px;
  background-color: #0e58a3;
  border-radius: 50%;
  margin-right: 6px;
}
</style>



